* {
  padding: 0;
  margin: 0;
  border: 0; }

nav,
footer,
header,
aside {
  display: block; }

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%; }

* {
  box-sizing: border-box; }

.wrapper {
  min-height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column; }

.container {
  max-width: 1170px;
  margin: 0 auto;
  padding: 0px 15px;
  width: 100%; }

.content {
  flex: 1 1 auto; }

body {
  font-family: "Open Sans", sans-serif; }

.header {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 50;
  min-height: 800px;
  padding: 0px 0px 50px 0px;
  display: flex;
  flex-direction: column; }
  @media (max-width: 767px) {
    .header {
      min-height: 600px; } }
  .header::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2; }
  .header__row {
    position: relative;
    display: flex;
    justify-content: space-between;
    height: 80px;
    align-items: center; }
  .header__logo {
    display: flex;
    align-items: center;
    position: relative;
    z-index: 4; }
  .header__signature {
    font-size: 13px;
    letter-spacing: 1px;
    color: #ffffff;
    font-weight: 300;
    display: block;
    margin: 0px 0px -7px 50px;
    position: relative; }
    .header__signature::before {
      content: "";
      position: absolute;
      width: 2px;
      height: 100%;
      background-color: #ffffff;
      top: 0;
      left: -21px; }
  .header__burger {
    display: none; }
  .header__menu {
    position: relative;
    z-index: 2; }
  .header__menu-item {
    display: inline-flex;
    list-style: none;
    margin: 0px 0px 0px 32px;
    transition: all 0.2s ease;
    border-top: 1px solid transparent; }
    .header__menu-item a {
      text-decoration: none;
      font-size: 13px;
      letter-spacing: 1px;
      color: #ffffff;
      font-weight: 400;
      transition: all 0.5s ease;
      display: inline-block; }
      .header__menu-item a:hover {
        color: #0feb9e; }
  .header__content {
    flex: 1 1 auto;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    padding: 0px 15px 100px 15px;
    position: relative; }
  .header__slider {
    min-width: 0; }
  .header__social {
    display: flex;
    justify-content: center;
    position: relative;
    z-index: 2; }
  .header__social-link {
    text-decoration: none;
    display: block;
    margin: 0px 9px; }

.logo {
  display: block;
  width: 90px; }
  .logo a {
    max-width: 100%;
    display: block;
    height: auto; }

.slider {
  min-width: 0; }
  .slider__title {
    margin: 0px 0px 30px 0px; }
  .slider__subtitle {
    font-size: 48px;
    color: #ffffff;
    font-weight: 300;
    margin: 0px 0px 35px 0px; }
    @media (max-width: 767px) {
      .slider__subtitle {
        font-size: 36px; } }
    @media (max-width: 480px) {
      .slider__subtitle {
        font-size: 26px; } }
    @media (max-width: 366px) {
      .slider__subtitle {
        font-size: 22px; } }
    .slider__subtitle span {
      color: #0feb9e; }
  .slider__description {
    display: block;
    font-size: 18px;
    color: #ffffff;
    font-weight: 300; }
    .slider__description::after {
      position: relative;
      display: inline-block;
      content: "";
      right: -21px;
      top: 50%;
      width: 28px;
      height: 1px;
      vertical-align: middle;
      background: #0feb9e; }
      @media (max-width: 480px) {
        .slider__description::after {
          display: none; } }
    .slider__description::before {
      position: relative;
      display: inline-block;
      content: "";
      left: -21px;
      top: 50%;
      width: 28px;
      height: 1px;
      vertical-align: middle;
      background: #0feb9e; }
      @media (max-width: 480px) {
        .slider__description::before {
          display: none; } }

.title {
  font-size: 62px;
  color: #ffffff;
  font-weight: 300; }
  @media (max-width: 767px) {
    .title {
      font-size: 48px; } }
  @media (max-width: 480px) {
    .title {
      font-size: 36px; } }
  .title span {
    font-weight: 600; }

.twitter,
.facebook,
.google-plus,
.dribbble {
  fill: #4f565d;
  height: 18px;
  width: 19px; }
  .twitter:hover,
  .facebook:hover,
  .google-plus:hover,
  .dribbble:hover {
    fill: #ffffff; }

.slick-dots {
  position: relative;
  z-index: 2;
  position: absolute;
  top: 50%;
  right: 47px;
  transform: translateY(-50%); }
  @media (max-width: 480px) {
    .slick-dots {
      right: 25px; } }

.slick-dots li {
  list-style: none;
  margin: 7px 0px; }
  .slick-dots li button {
    border: 1px solid #333a3e;
    background-color: transparent; }

.slick-dots li.slick-active button {
  background-color: #ffffff;
  border: 1px solid #ffffff;
  outline: none; }

.slick-dots button {
  font-size: 0;
  width: 10px;
  height: 10px;
  background-color: #ffffff;
  border-radius: 50%; }

@media (max-width: 767px) {
  body.lock {
    overflow: hidden; }
  /*Делаем размер Шапки меньше*/
  .header__row {
    height: 50px; }
  /*Показываем бургер и задаём стили*/
  .header__burger {
    display: block;
    position: relative;
    width: 30px;
    height: 20px;
    z-index: 4; }
  .header__burger:before,
  .header__burger:after {
    content: "";
    background-color: #fff;
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    transition: all 0.3s ease 0s; }
  .header__burger:before {
    top: 0; }
  .header__burger:after {
    bottom: 0; }
  .header__burger.active:before {
    transform: rotate(45deg);
    top: 9px; }
  .header__burger.active:after {
    transform: rotate(-45deg);
    bottom: 9px; }
  .header__burger.active span {
    transform: scale(0); }
  .header__burger span {
    position: absolute;
    background-color: #fff;
    left: 0;
    width: 100%;
    height: 2px;
    top: 9px;
    transition: all 0.3s ease 0s; }
  /**/
  /*Стилизация меню*/
  .header__nav {
    position: fixed;
    top: -100%;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: #1ea78d;
    z-index: 3;
    padding: 70px 10px 20px 10px;
    transition: all 0.3s ease 0s; }
  .header__nav.active {
    top: 0; }
  .header__menu {
    display: flex;
    flex-direction: column; }
  .header__menu li {
    margin: 0px 0px 20px 0px; }
    .header__menu li a {
      font-size: 28px; } }

@media (max-width: 320px) {
  .header__signature {
    margin: 0 0 -7px 40px; } }
